/* Demo Styles */

.main-content {
  color: $body-color;
  font-size: 16px;
  > .page-title {
    margin-bottom: 1rem;
    font-weight: 400;
  }
  > h4 {
    margin-top: 1.5rem;
    margin-bottom: .875rem;
    &::before {
      display: block;
      height: 5.4rem;
      margin-top: -6rem;
      content: "";
    }
  }
  > hr {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .example {
    font-size: 0.875rem;
    letter-spacing: normal;
    padding: 10px;
    background-color: $card-bg;
    border: 4px solid $border-color;
    position: relative;
    @media(min-width: 576px) {
      padding: 25px;
    }
  }
  .highlight {
    position: relative;
    background-color: $card-bg;
    padding: 15px 29px;
    margin: 0 0 1rem;
    ngb-tabset {
      .nav-tabs {
        .nav-item {
          .nav-link {
            background-color: #fff;
            font-size: .85rem;
            border: 0;
            color: $text-muted;
            padding: .5rem 2.5rem;
            &.active {
              border-bottom: 2px solid theme-color(primary);
              color: $body-color;
            }
          }
        }
      }
      pre {
        padding: 15px 0;
        font-size: .875rem;
        font-family: $type1;
        background: transparent;
        line-height: 1.4;
        margin: 0;
        code {
          font-family: $type1;
          padding: 0;
          tab-size: 6;
          word-spacing: 2px;
          color: $body-color;
          text-shadow: none;
          line-height: 1.35rem;
          :host-context(.rtl) & {
            direction: ltr;
          }
          .token {
            &.url,
            &.string,
            &.entity,
            &.operator {
              background: none;
            }
          }
        }
      }
      .btn-clipboard {
        position: absolute;
        top: 66px;
        right: 29px;
        font-size: 14px;
        padding: 4px 6px;
        background: darken($content-bg, 6%);
        @media(max-width: 991px) {
          padding: 2px 6px;
        }
        &:hover {
          background: darken($content-bg, 8%);
        }
        i {
          font-size: 18px;
          @media(max-width: 991px) {
            font-size: 15px;
          }
        }
      }
    }
  }
}

.example {
  .btn-toolbar {
    + .btn-toolbar {
      margin-top: .5rem;
    }
  }
  .modal {
    &.static {
      position: static;
      display: block;
    }
  }
  .navbar {
    position: relative;
    padding: .5rem 1rem;
    left: auto;
    width: 100%;
    height: auto;
    z-index: 9;
    .navbar-brand {
      font-size: 1.25rem;
    }
  }
  .progress {
    + .progress {
      margin-top: 10px;
    }
  }
  .perfect-scrollbar-example {
    background: $card-bg;
  }
  .scrollspy-example {
    position: relative;
    height: 200px;
    margin-top: .5rem;
    overflow: auto;
  }
  .scrollspy-example-2 {
    position: relative;
    height: 350px;
    overflow: auto;
  }
  nav {
    .breadcrumb {
      margin-bottom: .75rem;
    }

    &:last-child {
      .breadcrumb {
        margin-bottom: 0;
      }
    }
  }
}

.page-breadcrumb {
  margin-bottom: 15px;
  .breadcrumb {
    padding: 0;
    background: $content-bg;
  }
}

.noble-ui-logo {
  font-weight: 900;
  font-size: 25px;
  letter-spacing: -1px;
  color: #031a61;
  span {
    color: theme-color(primary);
    font-weight: 300;
  }
  &:hover {
    color: #031a61;
  }
  &.logo-light {
    color: $body-color;
  }
}

.rtl {
  .page-content {
    .main-content {
      .example {
        .navbar {
          right: 0;
        }
      }
    }
  }
}
